body {
	margin: 0;
	font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale
}

code {
	font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace
}

h2 {
	font-size: 32px;
	color: #000514
}

h1,
h2,
h3,
h4 {
	line-height: normal
}

.hero-banner {
	align-items: flex-start;
	background-position: 100% 0;
	display: flex;
	flex-direction: column;
	height: 516px;
	justify-content: center;
	padding: 18px 0 0 57px;
	background-color: #00293f;
	background-size: cover;
	background-image: url(./images/1190_en@1x.22395f50.22395f50.jpg)
}

@media (max-width:700px) {
	.hero-banner {
		align-items: flex-end;
		display: flex;
		flex-direction: unset;
		background-position: top;
		background-repeat: no-repeat;
		height: 528px;
		padding: 48px 57px
	}
}

@media (max-width:550px) {
	.hero-banner {
		height: 528px;
		padding: 0 24px 32px
	}
}

@media (max-width:1190px) {
	.hero-banner {
		background-image: url(./images/1190_en@1x.22395f50.22395f50.jpg)
	}
}

@media (max-width:1024px) {
	.hero-banner {
		background-image: url(./images/1024_en@1x.1e21351b.1e21351b.jpg)
	}
}

@media (max-width:950px) {
	.hero-banner {
		background-image: url(./images/950_en@1x.b831dd70.b831dd70.jpg)
	}
}

@media (max-width:700px) {
	.hero-banner {
		background-image: url(./images/700_en@1x.a7fbf14e.a7fbf14e.jpg)
	}
}

.hero-banner-title {
	font-size: 52px;
	margin: 0 0 16px;
	max-width: 560px;
	color: #fff;
	font-weight: 600;
	text-shadow: 0 2px 4px rgba(0, 0, 0, .5)
}

@media (max-width:700px) {
	.hero-banner-title {
		font-size: 42px
	}
}

@media (max-width:550px) {
	.hero-banner-title {
		font-size: 32px;
		margin-bottom: 8px
	}
}

.line1 {
	font-size: 22px;
	font-weight: 400;
	margin-bottom: 32px;
	color: #fff;
	text-shadow: 0 2px 4px rgba(0, 0, 0, .5)
}

@media (max-width:550px) {
	.line1 {
		font-size: 16px;
		margin-bottom: 24px
	}
}

.btn1 {
	background: #fff;
	color: #001927;
	border-radius: 4px;
	border: 1px solid #1e7b85;
	box-sizing: border-box;
	cursor: pointer;
	display: inline-block;
	font-size: 18px;
	font-weight: 600;
	line-height: 1.3em;
	padding: 12px 24px;
	position: relative;
	text-align: center;
	margin-bottom: 12px
}

@media (max-width:550px) {
	.btn1 {
		width: 100%
	}
}

.line2 {
	color: #fff;
	font-size: 16px;
	font-weight: 400;
	text-shadow: 0 2px 4px rgba(0, 0, 0, .5)
}

@media (max-width:550px) {
	.line2 {
		font-size: 14px
	}
}

.sec1 {
	background-color: #fff;
	padding: 64px 48px
}

.q-body,
.sec1 {
	align-items: center;
	display: flex;
	flex-direction: column
}

@media (max-width:700px) {
	.sec1 {
		padding: 48px 24px
	}
}

.heading1 {
	font-size: 36px;
	font-weight: 700;
	color: #000514;
	margin: 0 auto 16px;
	max-width: 792px;
	text-align: center
}

@media (max-width:700px) {
	.heading1 {
		font-size: 28px
	}
}

@media (max-width:550px) {
	.heading1 {
		font-size: 28px
	}
}

.list1 {
	list-style: none;
	margin: 0;
	padding: 0
}

@media (max-width:1024px) {
	.list1 {
		max-width: 444px
	}
}

@media (max-width:550px) {
	.list1 {
		max-width: 296px
	}
}

@media (max-width:320px) {
	.list1 {
		max-width: 296px
	}
}

.sub-li {
	display: inline-flex
}

@media (max-width:550px) {
	.sub-li {
		width: 50%
	}
}

.sub-div {
	align-items: center;
	display: flex;
	flex-direction: column;
	height: 123px;
	justify-content: center;
	width: 148px
}

.sub-heading {
	font-size: 18px;
	font-weight: 400;
	margin: 0;
	padding-top: 16px
}

.sub-div .icon {
	color: #57617a;
	font-size: 32px
}

.sec2 {
	background-color: #fafbfd;
	display: flex;
	justify-content: center;
	padding: 48px 0
}

.q-body {
	margin: initial;
	position: relative;
	width: 710px
}

.q-heading {
	font-size: 24px;
	font-style: italic;
	font-weight: 600;
	margin: 0;
	max-width: 588px;
	padding-bottom: 16px;
	text-align: center;
	z-index: 2
}

@media (max-width:950px) {
	.q-heading {
		padding: 0 40px 16px
	}
}

.q-footer {
	font-size: 18px;
	font-style: normal;
	font-weight: 400
}

.q-icon {
	background: url(data:image/png;base64,);
	background-position: 50%;
	background-repeat: no-repeat;
	background-size: contain;
	height: 46px;
	left: 0;
	position: absolute;
	top: -8px;
	width: 62px;
	z-index: 1
}

@media (max-width:950px) {
	.q-icon {
		left: 25px
	}
}

.c-sec {
	background-color: #5a1d41;
	display: flex;
	flex-direction: column;
	justify-content: flex-start;
	text-align: center;
	padding: 50px;
	min-width: 320px;
	box-sizing: border-box
}

@media (max-width:667px) {
	.c-sec {
		padding: 24px
	}
}

.c-sec h2 {
	font-weight: 400;
	color: #fafbfd;
	text-transform: uppercase;
	line-height: 1em;
	margin-bottom: 24px
}

.c-sec .document_grid {
	width: 876px;
	margin: 0 auto;
	padding: 0;
	overflow: hidden
}

@media (min-width:668px) and (max-width:1025px) {
	.c-sec .document_grid {
		width: 100%
	}
}

@media (max-width:667px) {
	.c-sec .document_grid {
		width: 66%
	}
}

@media (max-width:475px) {
	.c-sec .document_grid {
		width: 100%
	}
}

.c-sec .document_grid li {
	display: inline-block
}

@media (max-width:667px) {
	.c-sec .document_grid li:nth-child(n+5) {
		display: none
	}
}

.c-sec .doc_thumb {
	display: inline-block;
	width: 108px;
	height: 144px;
	background-size: cover;
	background-repeat: no-repeat;
	margin: 1px 3px;
	box-shadow: 1px 2px 4px rgba(0, 0, 0, .4)
}

.c-sec .doc_thumb.is_audiobook {
	width: 144px
}

.c-sec .cta_row {
	padding-top: 24px
}

@media (max-width:700px) {
	.c-sec .cta_row {
		padding: 15px 0
	}
}

.c-sec .cta_row .flat_btn {
	line-height: 1.3em;
	padding-bottom: 11px;
	padding-top: 11px;
	white-space: normal
}

@media (max-width:667px) {
	.c-sec .flat_btn {
		display: block;
		min-width: 256px;
		white-space: nowrap;
		margin: auto
	}
}

.flat_btn,
.flat_btn:visited {
	color: #fff
}

.flat_btn {
	box-sizing: border-box;
	display: inline-block;
	border-radius: 4px;
	border: 1px solid hsla(0, 0%, 100%, .2);
	font-size: 18px;
	font-weight: 700;
	min-height: 2.44444em;
	line-height: 2.44444em;
	min-width: 120px;
	padding: 0 30px;
	text-align: center;
	text-transform: capitalize;
	background-color: #1e7b85
}

.flat_btn,
a {
	cursor: pointer
}

a {
	color: #1e7b85;
	text-decoration: none;
	transition: color .2s ease-in-out;
	-moz-transition: color .2s ease-in-out;
	-webkit-transition: color .2s ease-in-out
}

.c-sec .cta_row .under_cta {
	color: #fafbfd;
	max-width: 300px;
	margin: 10px auto 0
}

.c-sec .under_cta {
	text-align: center;
	font-weight: 700;
	padding-top: 5px;
	color: #57617a
}

.press-sec {
	background-color: #fafbfd;
	text-align: center;
	padding: 14px;
	min-width: 320px;
	box-sizing: border-box
}

@media (max-width:667px) {
	.press-sec {
		padding: 24px
	}
}

.press-sec .heading {
	text-transform: uppercase;
	margin-top: 0;
	margin-bottom: 30px;
	color: #00293f;
	opacity: .75;
	font-size: 14px;
	font-weight: 400
}

.press-sec .brands_container {
	display: flex;
	flex-direction: row;
	justify-content: space-between;
	width: 85%;
	max-width: 950px;
	margin: auto;
	padding: 0
}

@media (max-width:667px) {
	.press-sec .brands_container {
		flex-direction: column
	}
}

.press-sec .logo {
	display: inline-block
}

@media (max-width:667px) {
	.press-sec .logo {
		display: block;
		width: auto;
		margin: 20px auto
	}
}

.f-sec {
	background-color: #f0f2f9;
	padding: 40px 0
}

.f-container {
	display: flex;
	flex-direction: column;
	align-items: center
}

.f-grid {
	box-sizing: border-box;
	-moz-column-gap: 24px;
	grid-column-gap: 24px;
	column-gap: 24px;
	display: grid;
	min-width: 320px;
	padding: 0 24px;
	width: 100%;
	grid-template-columns: repeat(12, 1fr);
	max-width: 1248px
}

@media (max-width:1008px) {
	.f-grid {
		grid-template-columns: repeat(12, 1fr);
		max-width: 1008px
	}
}

@media (max-width:808px) {
	.f-grid {
		grid-template-columns: repeat(8, 1fr);
		max-width: 808px
	}
}

@media (max-width:512px) {
	.f-grid {
		grid-template-columns: repeat(4, 1fr);
		max-width: 512px
	}
}

@media (max-width:320px) {
	.f-grid {
		grid-template-columns: repeat(4, 1fr);
		max-width: 320px
	}
}

@media (max-width:360px) {
	.f-grid {
		grid-template-columns: repeat(4, 1fr);
		max-width: 360px
	}
}

.f-grid-sec {
	position: relative;
	grid-column: auto/span 2;
	width: 100%;
	margin-bottom: 24px
}

@media (max-width:1008px) {
	.f-grid-sec {
		grid-column: auto/span 2
	}
}

@media (max-width:808px) {
	.f-grid-sec {
		grid-column: auto/span 2
	}
}

@media (max-width:512px) {
	.f-grid-sec {
		grid-column: auto/span 2
	}
}

@media (max-width:360px) {
	.f-grid-sec {
		grid-column: auto/span 2
	}
}

@media (max-width:320px) {
	.f-grid-sec {
		grid-column: auto/span 2
	}
}

.f-heading {
	font-weight: 400;
	font-size: 1rem;
	line-height: 1.3;
	font-family: "Source Serif Pro", sans-serif;
	font-weight: 600;
	font-style: normal;
	color: #303140;
	margin-top: 0
}

.f-ul,
.f-ul li {
	line-height: inherit
}

.f-ul {
	list-style: none;
	padding: 0;
	margin: 0
}

.f-ul li {
	padding-bottom: 8px
}

.f-link {
	font-size: .875rem;
	line-height: 1.5;
	color: #303140;
	margin: 0;
	text-align: left;
	font-weight: 400;
	font-style: normal;
	font-size: 1rem;
	background-color: transparent;
	border: none;
	display: inline-block;
	cursor: pointer;
	padding: 0;
	min-width: -webkit-fit-content;
	min-width: -moz-fit-content;
	min-width: fit-content
}

.f-subtext {
	align-items: center;
	display: flex
}